import * as React from 'react';
import "../styles/testnav.css"
import logo from '../images/6cars/logo.png'
import { Link } from 'gatsby';

const Testnav = () => {
   
    
    // function Click(){   
    //     const body = document.querySelector('body')as HTMLBodyElement;
        
    // if (body.style.overflow == "hidden") {
    //     body.style.overflow ="overlay";
        
    // }
    //  else{
    //     body.style.overflow ="hidden"
    //  }   
    // }
    // function linkClick (){
    //     const body = document.querySelector('body')as HTMLBodyElement;
    //     const checkbox = document.querySelector('#nav-tog') as HTMLInputElement;
    //             checkbox.checked=false;
    //             body.style.overflow ="overlay";   
    // }    
    return (
        <header className='main-header'>
    <div className="menu-wrap">
      
        <a  target='_blank' className="logo-wrap">
            <img className="logo" src={logo} alt="Logo" />
        </a>
        
        
       
          <input type='checkbox' id='nav-tog' />


        <label htmlFor='nav-tog'  className= "drop-trigger"onClick={()=>{
            const body = document.querySelector('body')as HTMLBodyElement;
        
            if (body.style.overflow == "hidden") {
                body.style.overflow ="overlay";
                
            }
             else{
                body.style.overflow ="hidden"
             }   
        }}  >
        
            <span className="btn-bar top" ></span>
            <span className='btn-bar middle'></span>
            <span className='btn-bar bottom'></span>
            
        </label>

    
        <nav className="main-nav">
    
            <label htmlFor='nav-tog' className='nav-revert'></label>

            <a href='#home' className="nav-item" onClick={()=>{
                 const body = document.querySelector('body')as HTMLBodyElement;
                 const checkbox = document.querySelector('#nav-tog') as HTMLInputElement;
                         checkbox.checked=false;
                         body.style.overflow ="overlay";  
            }}>
               <span> <Link to= "/">Home</Link> </span>
            </a>
            <a href='#packages' className="nav-item"onClick={()=>{
                 const body = document.querySelector('body')as HTMLBodyElement;
                 const checkbox = document.querySelector('#nav-tog') as HTMLInputElement;
                         checkbox.checked=false;
                         body.style.overflow ="overlay";  
            }}>
                <span> Popular Place </span>
            </a>
            <a href='#about' className="nav-item"onClick={()=>{
                 const body = document.querySelector('body')as HTMLBodyElement;
                 const checkbox = document.querySelector('#nav-tog') as HTMLInputElement;
                         checkbox.checked=false;
                         body.style.overflow ="overlay";  
            }}>
                <span> About Us </span>
            </a>

               <a href='#service' className="nav-item"onClick={()=>{
                 const body = document.querySelector('body')as HTMLBodyElement;
                 const checkbox = document.querySelector('#nav-tog') as HTMLInputElement;
                         checkbox.checked=false;
                         body.style.overflow ="overlay";  
            }}>
               <span> Services </span>
            </a>
            <a href='#gallery' className="nav-item"onClick={()=>{
                 const body = document.querySelector('body')as HTMLBodyElement;
                 const checkbox = document.querySelector('#nav-tog') as HTMLInputElement;
                         checkbox.checked=false;
                         body.style.overflow ="overlay";  
            }}>
                <span> Gallery </span>
            </a>
            <a href='#contact' className="nav-item"onClick={()=>{
                 const body = document.querySelector('body')as HTMLBodyElement;
                 const checkbox = document.querySelector('#nav-tog') as HTMLInputElement;
                         checkbox.checked=false;
                         body.style.overflow ="overlay";  
            }}>
                <span> Contact </span>
            </a>
             <a href='tel:+91 6369103360' className="nav-item" onClick={()=>{
                 const body = document.querySelector('body')as HTMLBodyElement;
                 const checkbox = document.querySelector('#nav-tog') as HTMLInputElement;
                         checkbox.checked=false;
                         body.style.overflow ="overlay";  
            }}>
                <button className='py-2 px-4 bg-red-500 rounded-full hover:bg-black transition-all ease-in-out delay-75 text-white' onClick={()=>{
                 const body = document.querySelector('body')as HTMLBodyElement;
                 const checkbox = document.querySelector('#nav-tog') as HTMLInputElement;
                         checkbox.checked=false;
                         body.style.overflow ="overlay";  
            }} > Book now </button>
            </a>
        </nav>
    </div>
  
</header>

    )
}

export default Testnav